#popMessage {
  position: fixed;
  z-index: 999999999;
  min-width: 15vw;
  max-width: 22vw;
  height: auto;
  top: auto;
  bottom: 0px;
  left: 0;
  padding: 2em;
  padding-left: 0;
  margin-left: 8vw;
}
#popMessage .message {
  background: rgba(17, 17, 17, 0.9);
  margin: 0;
  color: white;
  text-transform: capitalize;
  border-radius: 3px;
  font-weight: 500;
  padding: 15px;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.2);
  word-break: break-word;
  height: 0;
  padding: 0 1em;
  overflow: hidden;
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
}
#popMessage .message:nth-child(1) {
  margin-top: 0px;
}
#popMessage .message.show {
  margin-top: 1em;
  height: 100%;
  padding-top: 1.3em;
  padding-bottom: 1.3em;
}
@media only screen and (max-width: 1230px) {
  #popMessage {
    min-width: 20vw;
    max-width: 30vw;
    margin-left: 3vw;
  }
}
