.hero-input {
  display: flex;
  /*align-items: center;*/
  position: relative;
  cursor: text;
  padding: 0px;
  overflow: hidden;
  background: none;
  margin-bottom: 1em;
  box-shadow: inset 0 0px 8px 0 rgba(0, 0, 0, 0.06);
  color: inherit;
  font-size: 1em;
  /*<== in em*/
}
.hero-input .hero-input-label,
.hero-input .hero-input-field {
  padding: 0px 1.5em;
  font-size: 1em;
  text-align: left;
}
.hero-input .hero-input-label {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  margin: 0px 0px;
  color: inherit;
  top: auto;
  font-weight: 600;
  text-transform: capitalize;
  height: 100%;
  z-index: 0;
  background: none;
  -webkit-transition: height 300ms ease-out, padding 300ms ease-out;
  transition: height 300ms ease-out, padding 300ms ease-out;
  overflow: hidden;
}
.hero-input .hero-input-label > :nth-child(1) {
  width: 100%;
  white-space: nowrap;
  word-break: normal;
  word-wrap: normal;
  text-overflow: ellipsis;
  font-size: 1em;
  -webkit-transition: font-size 300ms ease-out;
  transition: font-size 300ms ease-out;
  overflow: hidden;
}
.hero-input .hero-input-label::first-letter {
  text-transform: capitalize !important;
}
.hero-input .hero-input-field {
  position: relative;
  display: block;
  width: 100%;
  margin-top: calc(1.2em +  0.5em  + 0.2em);
  padding-bottom: 0.5em;
  background: transparent;
  border: 0;
  border-bottom-color: transparent;
  outline: 0;
  resize: none;
}
.hero-input .hero-input-field:first-letter {
  text-transform: capitalize !important;
}
.hero-input.focused {
  border-color: #068fd5 !important;
}
.hero-input.focused .hero-input-label {
  padding-top: 0.5em;
  z-index: 1;
  height: calc( calc( calc(1em *  0.83 ) * 1.2 ) +  0.85em  ) !important;
}
.hero-input.focused .hero-input-label > :nth-child(1) {
  font-size: 0.83em !important;
}
.hero-input.filled .hero-input-label,
.hero-input.fixed-focused .hero-input-label {
  padding-top: 0.5em;
  z-index: 1;
  height: calc( calc( calc(1em *  0.83 ) * 1.2 ) +  0.85em  ) !important;
}
.hero-input.filled .hero-input-label > :nth-child(1),
.hero-input.fixed-focused .hero-input-label > :nth-child(1) {
  font-size: 0.83em !important;
}
.hero-input.mandatory {
  border-bottom: none;
}
.hero-input.mandatory .hero-input-field {
  border-bottom: solid 1px #ff516c;
}
